	module.exports = {"data":{"orga":{"html":"<blockquote>We now come to the decisive step of mathematical abstraction: we forget about what the symbols stand for. ...[The mathematician] need not be idle; there are many operations which he may carry out with these symbols, without ever having to look at the things they stand for.\n\nHermann Weyl, The Mathematical Way of Thinking</blockquote><div>We built abstractions by combining procedures to <i>compound procedures</i> in Chapter 1. Now we need the abstractions for combining data objects to form <i>compound data</i></div><div>For complex system, compound data is also important</div><ul><li>To elevate the conceptual level at which we can design our programs: Stop worrying low level strucutre like numerator and denominator. But instead, think of rational number directly. (class, ADT)</li><li>To increase the modularity of our designs: If we can manipulate rational numbers in their own right, then we can separate the part of our program that deals with rational numbers per se. The general technique of isolating the parts of a program that deal how data objects are used is a powerful design methodology called <i>data abstraction</i> (module?)</li><li>To use of compound data leads to a real increase in the expressive power of our programming language. Suppose ewe have this: (interface, typeclass)</li></ul><pre><code class=\"language-scheme\"><span class=\"token punctuation\">(</span><span class=\"token keyword\">define</span> <span class=\"token punctuation\">(</span><span class=\"token function\">linear-combination</span> a b x y<span class=\"token punctuation\">)</span>\n  <span class=\"token punctuation\">(</span><span class=\"token operator\">+</span> <span class=\"token punctuation\">(</span><span class=\"token operator\">*</span> a x<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">(</span><span class=\"token operator\">*</span> b y<span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span></code></pre><div>We could have this and express the idea that from linear combinations whenever addition and multiplication are defined -- for rational numbers, complex numbers, polynominals, or whatever:</div><pre><code class=\"language-scheme\"><span class=\"token punctuation\">(</span><span class=\"token keyword\">define</span> <span class=\"token punctuation\">(</span><span class=\"token function\">linear-combination</span> a b x y<span class=\"token punctuation\">)</span>\n  <span class=\"token punctuation\">(</span><span class=\"token function\">add</span> <span class=\"token punctuation\">(</span><span class=\"token function\">mul</span> a x<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">(</span><span class=\"token function\">mul</span> b y<span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span></code></pre><div>We will:</div><ul><li>Discover how to form compound data using no special \"data\" operations at all, only procedures. This will further blur the distinction between \"procedure\" and \"data\".</li><li>One Key idea with compound data is the notion of <i>closure</i> -- that the glue we use for combining data objects should allow us to combine</li></ul>","meta":{"title":"SICP Chapter2: Building Abstraction with Data","date":"2018-10-09"}}},"pathContext":{"slug":"/sicp-ch2/"}}

